#divprincipal{
  background-color:rgba(10,10,10,.45);
  border-radius:30px;
}

#divprincipal button{
  text-align: center;
}

#pforecasttitle{
  color: white;
}

#divcontentdailyfore{
  overflow:scroll;
  display:flex;
  height:110px;
}

#divcontentdailyfore::-webkit-scrollbar{
  width: 0;
  height: 8px;
  background-color: gray;
}

#divreturndaily{
  float:left;
  width:100px;
  height:100px;
}

#p1divret{
  color:white;
  font-size:11px;
  width:70px;
}

#p2divret{
  color:white;
}

#closeBtn{
  color: rgba(255,255,255,.6);
  background: none;
  border: none;
  height: 30px;
  width: 100px;
  font-size: 20px;
  padding: 0;
  transition: .3s ease-in-out all;
}

#closeBtn:hover{
  color: rgba(255,50,50,.7);
}
