#divret{
  min-height:100vh;
  background-image: url('./Resourses/pexels-johannes-plenio-1118873.jpg');
  background-position:center;
  background-size:cover;
  position:relative;
}

#divcont{
  position:sticky;
  top:0px;
}

h1{
  color:rgba(0,0,0,.6);
  font-family: inherit;
  font-weight: bold;
}

span{
  color:rgba(255,255,255,.6);
}

#btnApp{
  color:rgba(155,155,155,.8);
  outline:2px ridge rgba(170, 170, 170, .4);
  border-radius: 10px;
  background-color:rgba(50,50,50,.3);
  font-size:18px;
  font-weight: bold;
  border:0;
}
